import React from 'react'
import FontAwesome from 'react-fontawesome';
import { Link } from "gatsby"
import {Helmet} from "react-helmet"
import { rhythm } from '../utils/typography'

const Place = props => (
    <a style={{padding: rhythm(.5)}} href={props.link}><FontAwesome name={props.icon} style={{paddingRight: rhythm(0.4)}}/>{props.name}</a>
)

export default () => (
    //reset title
    <>

    <Helmet title="Spacey's Stuff">
        <link rel="stylesheet" type="text/css" href="https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css" />
    </Helmet>
    <style dangerouslySetInnerHTML={{__html: `html { background-color: #292D3E; } div, h1, h2, h3 { color: #ffffff; } a { color: #82AAFF; background: none; text-shadow: none;}`}} />
    <h1 style={{textAlign: `center`}}>SpaceCats' Stuff</h1><br />
    <p style={{textAlign: `center`}}>What a horrible night to have an overly simple home page.</p>
    <p style={{textAlign: `center`}}>Here are some places you can find my stuff.</p>
    
    <div style={{textAlign: `center`}}>
        <Place name="Github" link="https://github.com/SpaceCats64" icon="github"/>
        <Place name="Twitter" link="https://twitter.com/kittensandals" icon="twitter"/>
    </div>
    <br />
    <p style={{textAlign: `center`}}>I also have a blog here at the <Link to="/blog/">SpaceBlog<FontAwesome name="book" style={{marginLeft: rhythm(0.3)}}/></Link></p>
    </>
)